	
    .body{
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: center;
        /* margin-right: 9%; */
        align-items: center;
        min-height: 85vh;
        font-family: 'Jost', sans-serif;

     
        /* background: linear-gradient(to right ,#98a0a3,#dbe4e7,#d2dee2,#98a0a3); */
      
    }
    
   
    
    .main{
        width: 350px;
        height: 500px;
        background: linear-gradient(to right,#dbe4e7,#dbe4e7,#d2dee2,#dbe4e7);
        /* background: rgb(187, 105, 58); */
        /* background: rgba(189, 71, 35, 0.842); */
        /* background: rgb(63, 104, 126); */
        overflow: hidden;
    
        /* background: url("https://doc-08-2c-docs.googleusercontent.com/docs/securesc/68c90smiglihng9534mvqmq1946dmis5/fo0picsp1nhiucmc0l25s29respgpr4j/1631524275000/03522360960922298374/03522360960922298374/1Sx0jhdpEpnNIydS4rnN4kHSJtU1EyWka?e=view&authuser=0&nonce=gcrocepgbb17m&user=03522360960922298374&hash=tfhgbs86ka6divo3llbvp93mg4csvb38") no-repeat center/ cover; */
        border-radius: 10px;
        box-shadow: 5px 20px 50px #000;
    }
    #chk{
        display: none;
    }
    .signup{
        position: relative;
        width:100%;
        height: 100%;
    }
    .label1{
        color: #20505f;
        font-size: 2.3em;
        justify-content: center;
        display: flex;
        margin: 60px;
        font-weight: bold;
        cursor: pointer;
        transition: .5s ease-in-out;
    }
    .label{
        color: #20505f;
        font-size: 2.3em;
        justify-content: center;
        display: flex;
        margin: 60px;
        font-weight: bold;
        cursor: pointer;
        /* transition: .5s ease-in-out; */
    }
    .label2{
        color: #20505f;
        font-size: 1.7em;
        justify-content: center;
        display: flex;
        padding: 20px 0px 0px 0px;;
        margin: 60px;
        font-weight: bold;
        cursor: pointer;
        transition: .4s ease-in-out;
    }
    input{
        /* width: 60%; */
        /* height: 20px; */
        background: white;
        justify-content: center;
        display: flex;
        margin: 20px auto;
        padding: 5px;
        border: none;
        outline: none;
        border-radius: 5px;
    }
    .buttonLogin{
        width: 60%;
        height: 40px;
        margin: 10px auto;
        justify-content: center;
        display: block;
        color: #fff;
        background: #573b8a;
        font-size: 1em;
        font-weight: bold;
        margin-top: 20px;
        outline: none;
        border: none;
        border-radius: 5px;
        transition: .2s ease-in;
        cursor: pointer;
    }
    .button:hover{
        background: #6d44b8;
    }
    .login{
        height: 460px;
        background: #98a0a3;
        border-radius: 60% / 10%;
        transform: translateY(-180px);
        transition: .8s ease-in-out;
    }
    .login label{
        color: #300c72;
        transform: scale(.5);
    }
